<template>
	<div class="page4_2">
		<div class="nav">
			<ul class="fl">
				<li>
					<router-link active-class="active" class="item" :to="{path:'/inside/page4_2'}">
						报告内容
					</router-link>
				</li>
			</ul>
			<!-- <div class="fr inter">
				<interBtn title="报告内容" id="more4_2"/>
			</div> -->
		</div>
		<div class="content_page">
			<div class="page-top">
				<Place class="fl" :data="msg"/>
				<div class="fr inter">
					<interBtn title="报告内容" id="more4_2" ydname="审计报告自动生成"/>
				</div>
			</div>
			<div>
				<div class="box box-border-shadow">
					<el-form :rules="rules" status-icon ref="form1" style="width: 60%;margin: 0 auto" label-position="right" label-width="120px" :model="form1">
					    <el-form-item label="报告名称:" prop="title">
					       <el-input v-model="form1.title" placeholder="请输入报告名称"></el-input>
					    </el-form-item>
					    <el-form-item label="报告作者:" prop="author">
					   	   <el-input v-model="form1.author" placeholder="请输入报告作者"></el-input>
					    </el-form-item>
					    <el-form-item label="收件人:" prop="name">
					   	   <el-input v-model="form1.name" placeholder="请输入收件人"></el-input>
					    </el-form-item>
					    <el-form-item label="报告索引号:" prop="num">
					   	   <el-input v-model.number="form1.num" placeholder="请输入报告索引号"></el-input>
					    </el-form-item>
					    <el-form-item label="审计疑点选择:" prop="type">
					    	<el-select v-model="form1.type" placeholder="请选择要生成的审计报告疑点" style="display: block">
						       <el-option v-for="item in getWords" :label="item.name" :value="item.name"></el-option>
						    </el-select>
					    </el-form-item>
					    <!-- <el-form-item label="审计结论选择:" prop="type2">
					    	<el-select v-model="form1.type2" placeholder="请选择要生成的审计报告结论" style="display: block">
						       <el-option v-for="item in getWords" :label="item.name" :value="item.name"></el-option>
						    </el-select>
					    </el-form-item>
					    <el-form-item label="审计建议选择:" prop="type3">
					    	<el-select v-model="form1.type3" placeholder="请选择要生成的审计报告建议" style="display: block">
						       <el-option v-for="item in getWords" :label="item.name" :value="item.name"></el-option>
						    </el-select>
					    </el-form-item> -->
					    <div>
					    	<div v-if="tableData.length>0">
						    	<el-table
							    ref="multipleTable"
							    :data="tableData"
							    tooltip-effect="dark"
							    style="width: 100%"
							    @selection-change="handleSelectionChange">
								    <el-table-column
								      type="selection"
								      width="55">
								    </el-table-column>
								    <el-table-column
								      prop="审计疑点"
								      label="审计疑点"
								      width="160">
								    </el-table-column>
								    <el-table-column
								      prop="审计结论"
								      label="审计结论"
								      width="160"
								     >
								    </el-table-column>
								    <el-table-column
								      prop="审计建议"
								      label="审计建议"
								      show-overflow-tooltip
								     >
								    </el-table-column>
								</el-table>
						    </div>
						    <div v-else>
						    	<div style="line-height: 100px;height: 100px;text-align: center;font-size: 20px;color: #b80000">
									<i class="am-icon-spinner am-icon-spin"></i> loading
								</div>
						    </div>
					    </div>
					    <el-form-item>
						    <el-button type="primary" @click="submitForm('form1')">初稿生成</el-button>
						     <el-button @click="resetForm('form1')">重置</el-button>
						</el-form-item>
					</el-form>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import interBtn from './interactions'
	import Place from './page/place'
	import reqData from "../../reqData.js"
	export default{
		name:'page4_2',
		components:{
			interBtn,
			Place,
		},
		data(){
			var checkNum = (rule, value, callback) => {
		        if (!value) {
		          return callback(new Error('请输入报告索引号'));
		        }
		        setTimeout(() => {
		          if (!Number.isInteger(value)) {
		            callback(new Error('请输入数字值'));
		          } else {
		            callback();
		          }
		        }, 1000);
		    };
			return{
				msg:["审计报告自动生成","报告内容"],
				form1: {
		          title:'',
		          author: '',
		          name:'',
		          num:'',
		          type:''
		        },
		        rules: {
		          title: [
		            { required: true, message: '请输入报告名称', trigger: 'blur' },
		            // { min: 3, message: '长度大于3个字符', trigger: 'blur' }
		          ],
		          author: [
		            { required: true, message: '请输入报告作者', trigger: 'blur' }
		          ],
		          name: [
		            { required: true, message: '请输入收件人', trigger: 'blur' }
		          ],
		          type: [
		            { required: true, message: '请选择要生成的审计报告疑点', trigger: 'change' }
		          ],
		          num: [
		            { required: true,validator: checkNum, trigger: 'blur' }
		          ]
		        },
		        tableData:[],
		        multipleSelection: []
			}
		},
		computed:{
			getFormData(){
				console.log(JSON.parse(sessionStorage.getItem("baogaoForm")))
				return JSON.parse(sessionStorage.getItem("baogaoForm"));
			},
			getWords(){
				return JSON.parse(sessionStorage.getItem("WordCloud"));
			},
			getWord(){
				return this.$store.getters.getWord||sessionStorage.getItem("word");
			},
		},
	    methods: {
	    	handleSelectionChange(val) {
		       this.multipleSelection = val;
		    },
	    	submitForm(formName) {
		        this.$refs[formName].validate((valid) => {
		          if (valid) {
		          	let formData={
		          		title:this.form1.title,
		          		name:this.form1.name,
		          		author:this.form1.author,
		          		num:this.form1.num,
		          		type:this.form1.type
		          	};
		          	console.log(formData);
		            sessionStorage.setItem("baogaoForm",JSON.stringify(formData));  
	        		this.$router.push({ path: 'page4_3',params: { form: JSON.stringify(formData) }});
		          } else {
		            console.log('error submit!!');
		            return false;
		          }
		        });
		    },
		    resetForm(formName) {
		        this.$refs[formName].resetFields();
		    },
		    dataSelects(){
		    	reqData
		            .req({
		                apiName: "AuditDoubt",
		                data: {             
			                word: this.getWord
			            }
		            })
		            .then(res => {
		                res = res.data;
		                if (res && res.code === "200") {
		                	let data=res.data;
		                	console.log(data);
		                	this.tableData=data
		                }
		            })
		            .catch(err => {
		            	console.log(err);
		            });
		    }
	    },
	    mounted(){
			 this.dataSelects();
		},
	}
</script>
<style type="text/css" scoped="">
	.page4_2 .inter{
		height: 44px;
		line-height: 44px;
	}
	.page4_2 .box{
		min-height: 600px;
    	text-align: center; 
    	margin: 20px auto;
        width: 100%;
        padding: 60px 60px;
        box-sizing: border-box;
        text-align: justify;
        border-radius: 8px;
        background-color: #fff;
	}
</style>


